import Hello from '@/components/hello'
import test2 from '@/components/world'
let resultRouter = [
  {
    path: '/',
    name: 'Hello',
    component: Hello
  },
  {
    path: '/test2',
    name: 'Hello',
    component: test2
  }
]
export default resultRouter



